<script>
/* Code generated with AutoHTML Plugin for Figma */
import axios from "axios";

export default {
  components: {},
  props: {},
  data() {
    // quickfix to have components available to pass as props
    return {
      Config: [],
      serverConnect: [],
      FamilleCLT: [],
      familleCl: "",
      DescClient: "",
      Adresse: "",
      Ville: "",
      ICE: "",
      PATENTE: "",
      RC: "",
    };
  },
  mounted() {
    this.Config = require("../config.json");
    console.log(this.Config);
    this.serverConnect = this.Config.configApp;
    const instance = axios.create({
      baseURL: this.serverConnect.server_name,
      auth: {
        username: this.serverConnect.UsernameServer,
        password: this.serverConnect.AccessKeyServer,
      },
    });

    instance
      .get("FCVALUE" + `?$filter=Consolidation_Code eq '1'`)
      .then((response) => {
        this.FamilleCLT = response.data.value;
        console.log(response.data.value);
      })
      .catch((error) => {
        console.error(error);
      });
    const select = document.querySelector(".searchable-select select");
    const input = document.querySelector(".searchable-select input");
    const results = document.querySelector(
      ".searchable-select .search-results ul"
    );

    // Populate the results list with all options
    const options = Array.from(select.options);
    options.forEach((option) => {
      const li = document.createElement("li");
      li.textContent = option.textContent;
      li.dataset.value = option.value;
      results.appendChild(li);
    });

    // Handle input changes (filter options)

    // Handle option selection
    results.addEventListener("click", (e) => {
      if (e.target.tagName === "LI") {
        const selectedValue = e.target.dataset.value;
        input.value = e.target.textContent;
        select.value = selectedValue;
        results.parentElement.classList.remove("active");
      }
    });
  },
  methods: {
    createClient: function () {
      axios
        .create({
          baseURL: this.serverConnect.server_name2,
          auth: {
            username: this.serverConnect.UsernameServer,
            password: this.serverConnect.AccessKeyServer,
          },
        })
        .post("DirectionCodeUnit_NewClient?company='fgt'", {
          familleCl: this.familleCl,
          descClient: this.DescClient,
          adresse: this.Adresse,
          ville: this.Ville,
          ice: this.ICE,
          patente: this.PATENTE,
          rc: this.RC,
          vendeur: localStorage.getItem("CodeVendeur"),
        })
        .then(() => {
          //console.log(response.data.value);
          //this.Customer = response.data.value;
          window.location.reload();
        })
        .catch(() => {
          //console.error(error);
        });
    },
  },
};
</script>
