<script>
/* Code generated with AutoHTML Plugin for Figma */
import $ from "jquery";
import axios from "axios";

export default {
  components: {},
  props: {},
  data() {
    // quickfix to have components available to pass as props
    return {
      Config: [],
      serverConnect: [],
      username: "",
      password: "",
      search: "",
      Article: [],
      Articlebackup: [],
      ArticleRemise: [],
      Commande: [],
      checkedItems: [],
      targetArray: [],
      CountArticle: 0,
      ClientCode: "",
      ClientName: "",
      TotalHT: 0,
      TotalTTC: 0,
      TVA: 0,
      Facture: [],
      FactureSelected: [],
      AvoirSelected: [],
      TotalSelected: 0,
      Total: 0,
    };
  },
  methods: {
    openModal() {
      var modal = document.getElementById("paymentModal");
      modal.style.display = "block";
    },

    // Function to toggle the visibility of N° Cheque and Date Due inputs
    toggleInputs() {
      var paymentMethod = $("#paymentMethod").val();

      if (paymentMethod == "CHEQUE") {
        $("#numCheque").css("display", "block");
        $("#dateDue").css("display", "block");
        $("#numCheque2").css("display", "block");
        $("#dateDue2").css("display", "block");
      } else {
        $("#numCheque").css("display", "none");
        $("#dateDue").css("display", "none");
        $("#numCheque2").css("display", "none");
        $("#dateDue2").css("display", "none");
      }
    },
    handleCheckboxChange(index) {
      // This method is called when a checkbox is checked or unchecked
      console.log(`Checkbox ${index} changed: ${this.checkedItems[index]}`);
      if (this.checkedItems[index]) {
        this.FactureSelected.push(this.Facture[index]);
        const amountIncludingVAT = parseFloat(
          this.Facture[index].Amount_Including_VAT
        );
        const montantOuvert = parseFloat(this.Facture[index].Montant_Ouvert);
        this.TotalSelected += amountIncludingVAT - montantOuvert;
      } else {
        const amountIncludingVAT = parseFloat(
          this.Facture[index].Amount_Including_VAT
        );
        const montantOuvert = parseFloat(this.Facture[index].Montant_Ouvert);
        this.TotalSelected -= amountIncludingVAT - montantOuvert;

        // Find the index of the unchecked checkbox in FactureSelected
        const indexOfUnchecked = this.FactureSelected.findIndex(
          (item) => item === this.Facture[index]
        );
        if (indexOfUnchecked !== -1) {
          this.FactureSelected.splice(indexOfUnchecked, 1);
        }
      }

      console.log(this.FactureSelected);
      this.TotalSelected = 0;
      for (let i = 0; i < this.FactureSelected.length; i++) {
        this.TotalSelected =
          Number(this.TotalSelected) +
          Number(this.FactureSelected[i].Montant_Ouvert);
      }
      for (let j = 0; j < this.AvoirSelected.length; j++) {
        this.TotalSelected =
          Number(this.TotalSelected) -
          Math.abs(this.AvoirSelected[j].Montant_Ouvert);
      }
    },
    getRowStyle(row) {
      return {
        background: row.Document_Type === "Invoice" ? "#00b2eb" : "#f57373",
        // You can add more style properties here
      };
    },
    CalculateTot() {
      this.TotalTTC = this.Article.reduce(function (acc, item) {
        return (
          Number(acc) +
          Number(
            item.quantity *
              item.unitPrice *
              (1 - item.Remise / 100) *
              (1 + item.VAT / 100)
          )
        );
      }, 0);
      this.TotalHT = this.Article.reduce(function (acc, item) {
        return Number(acc) + Number(item.quantity * item.unitPrice);
      }, 0);
      this.TVA = this.TotalTTC - this.TotalHT;
    },
    validerEncaissement() {
      $("#start-animation").click();
      //  const currentDate = new Date();
      //  const year = currentDate.getFullYear();
      //  const month = String(currentDate.getMonth() + 1).padStart(2, "0"); // Months are 0-indexed
      // const day = String(currentDate.getDate()).padStart(2, "0");

      // const formattedDate = `${year}-${month}-${day}`;
      let dataObj = {
        lines: this.FactureSelected,
      };
      axios
        .create({
          baseURL: this.serverConnect.server_name2,
          auth: {
            username: this.serverConnect.UsernameServer,
            password: this.serverConnect.AccessKeyServer,
          },
        })
        .post("DirectionCodeUnit_PostPayment?company='fgt'", {
          jsonTextFacture: JSON.stringify(dataObj),
          espece: this.TotalSelected,
          cheque: 0,
          chequeNumber: "",
          codeEspece: "",
          tourNo: "",
          codevendeur: localStorage.getItem("CodeVendeur"),
          codeclient: localStorage.getItem("ClientCode"),
        })
        .then((response) => {
          console.log(response.data.value);
          alert(response.data.value);

          //this.Customer = response.data.value;
          // window.location.reload();
        })
        .catch(() => {
          //console.error(error);
        });
    },
    ArticleList() {
      if (this.Articlebackup.length > 0) {
        this.Article = this.Articlebackup;
      }
      $(".validation").css("visibility", "hidden");
    },
    ArticleCmd() {
      for (let i = 0; i < this.Articlebackup.length; i++) {
        for (let j = 0; j < this.Article.length; j++) {
          if (this.Article[j] == this.Articlebackup[i]) {
            this.Articlebackup[i].quantity = this.Article[j].quantity;
          }
        }
      }
      this.Articlebackup = this.Article;
      this.Article = $.grep(this.Article, function (item) {
        return item.quantity > 0;
      });
      $(".validation").css("visibility", "visible");
    },
    handleNumberInput() {},

    handleNumberInputBlur(id) {
      const input = this.inputs.find((input) => input.id === id);

      if (input.value < input.min) input.value = input.min;
      if (input.value > input.max) input.value = input.max;
    },
    setNumber(operation, id) {
      /*  this.Article = $.grep(this.Article, function (item) {
        return item.quantity > 0;
      });*/
      const value = $("#" + id).val();
      if (operation === "decrement") {
        if (value > 0) {
          $("#" + id).val(value - 1);
        }
      } else if (operation === "increment") {
        $("#" + id).val(Number(value) + 1);
      }

      this.handleNumberInput();
    },
    handleSubmit() {
      // Handle form submission if needed
    },
    clear: function (no) {
      var inputValue = $(".n" + no).val();
      if (inputValue === "0") {
        $(".n" + no).val("");
      }
    },
    add: function (item) {
      var inputValue = $(".n" + item.itemno).val();
      if (inputValue != "" && inputValue != "0") {
        // Check if the row already exists in targetArray
        item.quantity = inputValue;
        console.log(item.quantity);
        var existingIndex = this.Article.findIndex(
          (items) => items.itemno === item.itemno
        ); // Find the index of the row in targetArray

        this.Article[existingIndex].quantity = item.quantity;
        this.targetArray = $.grep(this.Article, function (item) {
          return item.quantity > 0;
        });
        this.CountArticle = this.targetArray.length;
      }
    },
  },
  mounted() {
    $("#start-animation").click(function () {
      $("#animate-this").fadeIn(1000).fadeOut(1000);
    });

    $("#stop-animation").click(function () {
      $("#animate-this").stop(true, true);
    });

    this.ClientCode = localStorage.getItem("ClientCode");
    this.ClientName = localStorage.getItem("ClientName");
    window.addEventListener("scroll", function () {
      // Prevent the default scroll behavior for the horizontal axis
      window.scrollTo({
        top: window.scrollY,
        left: 0, // Set the horizontal scroll position to 0
        behavior: "smooth", // You can use 'auto' or 'instant' as well
      });
    });
    $(".number-input-text-box").focus(function () {});

    this.Config = require("../config.json");
    console.log(this.Config);
    this.serverConnect = this.Config.configApp;
    const instance = axios.create({
      baseURL: this.serverConnect.server_name,
      auth: {
        username: this.serverConnect.UsernameServer,
        password: this.serverConnect.AccessKeyServer,
      },
    });
    var today = new Date();
    var dd = today.getDate();

    var mm = today.getMonth() + 1;
    var yyyy = today.getFullYear();
    if (dd < 10) {
      dd = "0" + dd;
    }

    if (mm < 10) {
      mm = "0" + mm;
    }
    today = yyyy + "-" + mm + "-" + dd;

    instance
      .get(
        `xouEcrituresComptClientEncaiss/?$filter=Salesperson_Code eq '${localStorage.getItem(
          "CodeVendeur"
        )}' and CodeClient eq '${localStorage.getItem("ClientCode")}' `,
        {}
      )
      .then((responsecompt) => {
        console.log(responsecompt.data.value);

        instance
          .get(`Facture_encaisse_Transaction`, {})
          .then((responseFct) => {
            console.log(responseFct.data.value);
            instance
              .get(`Avoir_encaisse_Transaction`, {})
              .then((responseAv) => {
                this.Facture = responsecompt.data.value;
                console.log(responseAv.data.value);
                for (let i = 0; i < this.Facture.length; i++) {
                  for (let j = 0; j < responseFct.data.value.length; j++) {
                    if (
                      this.Facture[i].Document_No_ ==
                      responseFct.data.value[j].Invoice_No_
                    ) {
                      this.Facture[i].Montant_Ouvert -=
                        responseFct.data.value[j].Amount;
                    }
                  }
                }
                for (let i = 0; i < this.Facture.length; i++) {
                  for (let j = 0; j < responseAv.data.value.length; j++) {
                    if (
                      this.Facture[i].Document_No_ ==
                      responseAv.data.value[j].Sales_Cr_Memo_No_
                    ) {
                      this.Facture[i].Montant_Ouvert = 0;
                    }
                  }
                }
                this.Facture = $.grep(this.Facture, function (product) {
                  return Math.abs(product.Montant_Ouvert) > 1;
                });
                this.Total = this.Facture.reduce(function (acc, currentValue) {
                  return acc + currentValue.Montant_Ouvert;
                }, 0);
              })
              .catch((error) => {
                console.error(error);
              });
          })
          .catch((error) => {
            console.error(error);
          });
      })
      .catch((error) => {
        console.error(error);
      });
  },

  computed: {
    filteredItem() {
      const filteredItems = this.Article.filter((item) => {
        // Convert both the Description and itemno properties to lowercase for case-insensitive search
        const description = item.Description.toLowerCase();
        const no = item.itemno.toLowerCase();
        const search = this.search.toLowerCase();

        // Check if either Description or itemno contains the search string
        return description.includes(search) || no.includes(search);
      });

      return filteredItems;
    },
  },
};
</script>
