<script>
/* Code generated with AutoHTML Plugin for Figma */
import axios from "axios";

export default {
  components: {},
  props: {},
  data() {
    // quickfix to have components available to pass as props
    return {
      Config: [],
      serverConnect: [],
      Commande: [],
      familleCl: "",
