import { createRouter, createWebHistory } from "vue-router";
import LoginForm from "../components/LoginForm.vue";
import main from "../components/HelloWorld.vue";
import ClientList from "../components/Client_List.vue";
import client_Card from "../components/client_Card.vue";
import Commande_Vte from "../components/Commande_Vte.vue";
import Facture_Vte from "../components/Facture_Vte.vue";
import CommandeList from "../components/ListCommande.vue";
import LanceCommande from "../components/LanceCommande.vue";
import AvoirVte from "../components/Avoir_Vte.vue";
import Encaissement_Vte from "../components/Encaissement_Vte.vue";
import Doc from "../components/MesDocument.vue";
const routes = [
  {
    path: "/",
    name: "LoginForm",
    component: LoginForm,
  },
  {
    path: "/Doc",
    name: "Doc",
    component: Doc,
  },
  {
    path: "/Encaissement_Vte",
    name: "Encaissement_Vte",
    component: Encaissement_Vte,
  },
  {
    path: "/AvoirVte",
    name: "AvoirVte",
    component: AvoirVte,
  },
  {
    path: "/LanceCommande",
    name: "LanceCommande",
    component: LanceCommande,
  },
  {
    path: "/CommandeList",
    name: "CommandeList",
    component: CommandeList,
  },
  {
    path: "/Facture_Vte",
    name: "Facture_Vte",
    component: Facture_Vte,
  },
  {
    path: "/Commande_Vte",
    name: "Commande_Vte",
    component: Commande_Vte,
  },
  {
    path: "/main",
    name: "main",
    component: main,
  },
  {
    path: "/ClientList",
    name: "ClientList",
    component: ClientList,
  },
  {
    path: "/client_Card",
    name: "client_Card",
    component: client_Card,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

router.beforeEach((to, from, next) => {
  const isAuthenticated = localStorage.getItem("username");
  if (to.name !== "LoginForm" && !isAuthenticated) {
    next({ name: "LoginForm" });
  } else if (to.name === "LoginForm" && isAuthenticated) {
    next({ name: "main" });
  } else {
    next();
  }
});

export default router;
