<script>
/* Code generated with AutoHTML Plugin for Figma */
import axios from "axios";

export default {
  components: {},
  props: {},
  data() {
    // quickfix to have components available to pass as props
    return {
      Config: [],
      serverConnect: [],
      Commande: [],
      familleCl: "",
      DescClient: "",
      Adresse: "",
      Ville: "",
      ICE: "",
      PATENTE: "",
      RC: "",
      TotalHT: 0,
      TotalTTC: 0,
      TVA: 0,
      document: "",
    };
  },
  mounted() {
    this.document = localStorage.getItem("N_Document");
    this.Config = require("../config.json");
    console.log(this.Config);
    this.serverConnect = this.Config.configApp;
    const instance = axios.create({
      baseURL: this.serverConnect.server_name,
      auth: {
        username: this.serverConnect.UsernameServer,
        password: this.serverConnect.AccessKeyServer,
      },
    });

    instance
      .get(
        "XouCommandeENT" +
          `?$filter=N_Document eq '${localStorage.getItem("N_Document")}'`
      )
      .then((response) => {
        this.Commande = response.data.value;
        console.log(response.data.value);
      })
      .catch((error) => {
        console.error(error);
      });

    // Handle input changes (filter options)

    // Handle option selection
  },
  methods: {
    LanceCmd: function () {
      axios
        .create({
          baseURL: this.serverConnect.server_name2,
          auth: {
            username: this.serverConnect.UsernameServer,
            password: this.serverConnect.AccessKeyServer,
          },
        })
        .post("DirectionCodeUnit_Lancecmd?company='fgt'", {
          ndoc: localStorage.getItem("N_Document"),
        })
        .then((response) => {
          console.log(response.data.value);
          //this.Customer = response.data.value;
          this.$router.go(-1);
        })
        .catch(() => {
          //console.error(error);
        });
    },
  },
};
