}

.number-input-container {
  width: 144px;

  display: grid;
  grid-template-columns: 48px auto 48px;
}

label {
  display: inline-block;

  margin-bottom: 4px;

  font-size: 12px;
  line-height: 24px;

  letter-spacing: 2px;
  text-transform: uppercase;

  color: var(--color-blue-dark);
}

.number-input {
  width: 100%;

  display: flex;
  flex-direction: row;

  background-color: var(--color-mustard-extra-light);

  overflow: hidden;
}

input[type="number"] {
  -webkit-appearance: none;
  -webkit-border-radius: 0px;
  -moz-appearance: none;
  appearance: none;

  position: relative;

  width: 100%;
  min-width: 100%;
  height: 48px;

  background-color: var(--color-mustard-extra-light);

  border: 1px solid var(--color-mustard-light);
  border-left: 1px solid rgba(0, 0, 0, 0);
  border-right: 1px solid rgba(0, 0, 0, 0);
  border-radius: none;

  font-size: 16px;
  line-height: 24px;
  text-align: center;

  transition: all 0.2s ease-out;
}

input[type="number"]:focus {
  background-color: white;

  border: 1px solid var(--color-mustard-midtone);

  outline: none;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

button {
  position: relative;

  height: 100%;

  margin-top: 32px;
  padding: 12px 16px;

  background-color: var(--color-mustard-extra-light);

  border: 1px solid var(--color-mustard-light);
  border-radius: none;

  transition: all 0.1s ease-out;

  cursor: pointer;

  -webkit-appearance: none;

  -webkit-transform: scale(1);
  transform: scale(1);
}

button {
  margin: 0;

  color: var(--color-black);
}

button:active,
button:focus {
  outline: none;
}

button::after {
  content: "";

  position: absolute;

  opacity: 1;

  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  transition: inherit;

  background-position: center;
  background-repeat: no-repeat;
}

button:disabled {
  pointer-events: none;
}

button:disabled::after {
  opacity: 0.25;
}

.button-decrement::after {
  background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 12H7' stroke='%23112C34' stroke-width='2' stroke-miterlimit='10' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
}

.button-increment::after {
