<script>
/* Code generated with AutoHTML Plugin for Figma */
import axios from "axios";
import $ from "jquery";

export default {
  components: {},
  props: {},
  data() {
    // quickfix to have components available to pass as props
    return {
      Config: [],
      serverConnect: [],
      username: "",
      password: "",
      Clients: [],
      search: "",
    };
  },
  created() {},
  methods: {
    alert: function (Client) {
      $("#openModalBtn").click();
      localStorage.setItem("GroupPrix", Client.Customer_Price_Group);
      localStorage.setItem("ClientCode", Client.No_);
      localStorage.setItem("ClientName", Client.Name);
    },
    buildFilterCriteria(username, password) {
      return `?$filter=No eq '${username}' and Password eq '${password}'`;
    },
    checkusers() {},

    //this.data = localStorage.getItem("isLoggedIn");
  },
  mounted() {
    this.Config = require("../config.json");
    console.log(this.Config);
    this.serverConnect = this.Config.configApp;
    const instance = axios.create({
      baseURL: this.serverConnect.server_name,
      auth: {
        username: this.serverConnect.UsernameServer,
        password: this.serverConnect.AccessKeyServer,
      },
    });

    instance
      .get(
        "XouClients/?$filter=Salesperson_Code eq '" +
          localStorage.getItem("CodeVendeur") +
          "'",
        {}
      )
      .then((response) => {
        this.Clients = response.data.value;
      })
      .catch((error) => {
        console.error(error);
      });
    // Get references to the modal and the buttons for opening and closing it
    const modal = document.getElementById("myModal");
    const openModalBtn = document.getElementById("openModalBtn");
    const closeModalBtn = document.getElementById("closeModalBtn");

    // Open the modal when the "Open Modal" button is clicked
    openModalBtn.addEventListener("click", () => {
      modal.style.display = "block";
    });

    // Close the modal when the "Close" button or outside the modal is clicked
    closeModalBtn.addEventListener("click", () => {
      modal.style.display = "none";
    });

    // Close the modal when the user clicks anywhere outside the modal
    window.addEventListener("click", (event) => {
      if (event.target === modal) {
        modal.style.display = "none";
      }
    });
  },
  computed: {
    filteredClient() {
      const filteredClient = this.Clients.filter((Client) => {
        // Convert both the Description and No_ properties to lowercase for case-insensitive search
        const Name = Client.Name.toLowerCase();
        const no = Client.No_.toLowerCase();
        const search = this.search.toLowerCase();

        // Check if either Description or No_ contains the search string
        return Name.includes(search) || no.includes(search);
      });

      return filteredClient;
    },
  },
};
</script>
